import selectSelfPro from './selectSelfPro.vue'
import { mapGetters, mapActions } from 'vuex'

export default {
	data() {
		return {
			list: [{
				name: '人气Top',
				id: 0,
				sizeArr: [{
					name: '规格',
					childlist: [{
						size: '大杯',
						isSelect: false
					}, {
						size: '中杯',
						isSelect: false
					}]
				}, {
					name: '温度',
					childlist: [{
						size: '冰',
						isSelect: false
					}, {
						size: '热',
						isSelect: false
					}]
				}, {
					name: '糖度',
					childlist: [{
						size: '全糖',
						isSelect: false
					}, {
						size: '半糖',
						isSelect: false
					}, {
						size: '无糖',
						isSelect: false
					}]
				}]
			}, {
				name: '店长推荐',
				id: 1
			}, {
				name: '今日推荐',
				id: 2
			}, {
				name: '精选臻品',
				id: 3
			}, {
				name: '最新上架',
				id: 4
			}, {
				name: '人气Top',
				id: 5
			}, {
				name: '店长推荐',
				id: 6
			}, {
				name: '今日推荐',
				id: 7
			}, {
				name: '精选臻品',
				id: 8
			}, {
				name: '最新上架',
				id: 9
			}],
			tabCur: 0,
			mainCur: 0,
			verticalNavTop: 0,
			load: true,
			sizeArr: [], //规格数组
			
			isShowSizeBox: false, //
			
			selectProH: 250, //
			isShowSelectList: false, //是否显示选择的产品列表
		}
	},
	components: {selectSelfPro},
	computed: {
		...mapGetters([
			'selfOrderObj'
		]),
		selectBoxH() {
			let sys = uni.getSystemInfoSync()
			return sys.windowWidth / 750 * 100 + this.selectProH
		}
	},
	onLoad() {
		
	},
	mounted() {
		
	},
	methods: {
		...mapActions([
			'toGetSelfOrder'
		]),
		// 选择产品数量
		toMinusNum: function() {
		  //   if (this.count > 1) {
				// this.totalPrice = this.$api.pointProblem(this.totalPrice, this.price, 'sub')
				// this.count--
		  //   }
		},
		toAddNum: function() {
			// this.count++
			// this.totalPrice = this.$api.pointProblem(this.totalPrice, this.price, 'add')
		},
		toJumpSureOrder() {
			uni.navigateTo({
				url: '/pages/selfPro/sureOrder'
			})
		},
		toShowSelectList() {
			this.isShowSelectList = !this.isShowSelectList
		},
		toCloseSelectList(e) {
			let sign = e.target.dataset.sign
			if(sign) {
				this.isShowSelectList = !this.isShowSelectList
			}
		},
		// 本页面
		toJumpSelectStore() {
			uni.navigateTo({
				url: '/pages/selfPro/selectStore'
			})
		},
		toSelectSelfPro(idx) {
			this.isShowSizeBox = true
			this.sizeArr = this.list[idx].sizeArr
			// this.toGetSelfOrder()
			/*
			1，通过id获取选择的产品信息
			2，加入购物车的产品加入到购物车列表中
			3，原产品列表添加一个是否被选中的状态，当产品列表或者购物车列表产品数量变化的时候，同步更新选择的数量
			4，通过产品数量计算购物车列表的高度，购物车列表高度不超过250px
			*/ 
		},
		toCloseBox(val) {
			this.isShowSizeBox = val
		},
		TabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.mainCur = e.currentTarget.dataset.id;
			this.verticalNavTop = (e.currentTarget.dataset.id - 1) * 50
		},
		VerticalMain(e) {



			let that = this;
			let tabHeight = 0;
			
			if (this.load) {
				for (let i = 0; i < this.list.length; i++) {
					let view = uni.createSelectorQuery().in(this).select("#main-" + this.list[i].id);
					view.fields({
						size: true
					}, data => {
						this.list[i].top = tabHeight;
						tabHeight = tabHeight + data.height;
						this.list[i].bottom = tabHeight;
					}).exec();
				}
				this.load = false
			}
			
			let scrollTop = e.detail.scrollTop + 10;
			for (let i = 0; i < this.list.length; i++) {
				if (scrollTop > this.list[i].top && scrollTop < this.list[i].bottom) {
					this.verticalNavTop = (this.list[i].id - 1) * 50
					this.tabCur = this.list[i].id
					return false
				}
			}
		}
	}
}
